:root {
  --main-ff: "OpenSans", sans-serif;
  --second-ff: "HelveticaNeueCyr", sans-serif;
  --color-dark: #000;
  --color-bg: #fff;
  --color-white: #fff;
  --color-green-light: #94c22b;
  --color-purple: #6c2f7c;
  --color-grey: #8a8a8a;
  --color-grey2: #a5a5a5;
  --color-grey-light: #fafafa;
  --border-color: rgba(160, 160, 160, 0.5);
  --grid-gap: 48px;
  --main-transition: 200ms;
  --color-error: #fd2600;
  --border-r: 10px;
}

@font-face {
  font-weight: 400;
  font-family: "OpenSans";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/OpenSans-Regular.woff2") format("woff2"), url("../fonts/OpenSans-Regular.woff") format("woff");
}

@font-face {
  font-weight: 600;
  font-family: "OpenSans";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/OpenSans-SemiBold.woff2") format("woff2"), url("../fonts/OpenSans-SemiBold.woff") format("woff");
}

@font-face {
  font-weight: 700;
  font-family: "OpenSans";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/OpenSans-Bold.woff2") format("woff2"), url("../fonts/OpenSans-Bold.woff") format("woff");
}

@font-face {
  font-weight: 500;
  font-family: "HelveticaNeueCyr";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/HelveticaNeueCyr-Medium.woff2") format("woff2"), url("../fonts/HelveticaNeueCyr-Medium.woff") format("woff");
}

@font-face {
  font-weight: 700;
  font-family: "HelveticaNeueCyr";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/HelveticaNeueCyr-Bold.woff2") format("woff2"), url("../fonts/HelveticaNeueCyr-Bold.woff") format("woff");
}

.button-reset {
  margin: 0;
  padding: 0;
  border-radius: 0;
  border: none;
  background-color: transparent;
  outline: none;
}

.logo {
  display: block;
  max-width: 24.5rem;
}

.button {
  display: inline-block;
  padding: 1.7rem;
  font-weight: 600;
  font-size: 1.6rem;
  line-height: 120%;
  text-transform: uppercase;
  border: none;
  border-radius: 10px;
  background-color: var(--color-green-light);
  color: var(--color-white);
  cursor: pointer;
  outline: none;
  transition: background-color var(--main-transition), color var(--main-transition);
}

.button:hover {
  background-color: #86af29;
}

.button:focus {
  background-color: #86af29;
}

.button_secondary {
  color: var(--color-dark);
  background-color: transparent;
  border: 1px solid var(--color-green-light);
  transition: background-color var(--main-transition), color var(--main-transition);
}

.button_secondary:hover {
  background-color: var(--color-green-light);
  color: var(--color-white);
}

.button_secondary:focus {
  background-color: var(--color-green-light);
  color: var(--color-white);
}

.button_iconed {
  display: flex;
  align-items: center;
  padding: 1.4rem 1.6rem;
  transition: fill var(--main-transition), background-color var(--main-transition), color var(--main-transition);
}

.button_iconed__icon {
  display: block;
  margin-right: 0.8rem;
  fill: var(--color-green-light);
  transition: fill var(--main-transition);
}

@media screen and (max-width: 992px) {
  .button_iconed__icon {
    fill: var(--color-white);
  }
}

.button_iconed:hover {
  background-color: var(--color-green-light);
}

.button_iconed:hover .button_iconed__icon {
  fill: var(--color-white);
}

.button_iconed:focus {
  background-color: var(--color-green-light);
}

.button_iconed:focus .button_iconed__icon {
  fill: var(--color-white);
}

@media screen and (max-width: 992px) {
  .button {
    border-color: var(--color-white);
    color: var(--color-white);
  }
}

.nav__list {
  display: flex;
  align-items: center;
}

.nav__item {
  margin-right: 4rem;
}

.nav__item:last-child {
  margin-right: 0;
}

.nav__link {
  font-weight: 600;
  font-size: 2.2rem;
  line-height: 2.6rem;
  transition: color var(--main-transition);
}

.nav__link:hover {
  color: var(--color-green-light);
}

.nav__link:focus {
  color: var(--color-green-light);
}

.markweber {
  display: flex;
  align-items: flex-start;
  font-size: 1.6rem;
  line-height: 1.9rem;
  margin-right: 5rem;
  color: var(--color-grey2);
}

.markweber img {
  margin-left: 1rem;
  width: 8.8rem;
  height: 2.2rem;
  object-fit: cover;
  transform: translateX(10px);
}

.site-second-title {
  font-weight: 600;
  font-size: 3.6rem;
  line-height: 4.3rem;
  margin: 0;
}

@media screen and (max-width: 575px) {
  .site-second-title {
    font-size: 3.33333rem;
    line-height: 4rem;
  }
}

.breadcrubs {
  display: flex;
  align-items: center;
  margin-bottom: 3rem;
}

.breadcrubs__item-link {
  display: flex;
  align-items: center;
  font-size: 1.8rem;
  line-height: 2.6rem;
  color: rgba(0, 0, 0, 0.5);
  transition: color var(--main-transition);
}

.breadcrubs__item-link:hover {
  color: var(--color-green-light);
}

.breadcrubs__item-link:hover .breadcrubs__item-icon {
  fill: var(--color-green-light);
}

.breadcrubs__item-link:focus {
  color: var(--color-green-light);
}

.breadcrubs__item-link:focus .breadcrubs__item-icon {
  fill: var(--color-green-light);
}

.breadcrubs__item-icon {
  fill: rgba(0, 0, 0, 0.5);
  margin-right: 1.4rem;
  transition: fill var(--main-transition);
  width: 0.9rem;
  height: 1.4rem;
}

@media screen and (max-width: 992px) {
  .breadcrubs {
    display: none;
  }
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  font-size: 10px;
  line-height: 1.2;
}

@media screen and (max-width: 1440px) {
  html {
    font-size: 9px;
  }
}

@media screen and (max-width: 1367px) {
  html {
    font-size: 7px;
  }
}

@media screen and (max-width: 1199px) {
  html {
    font-size: 6px;
  }
}

body {
  color: var(--color-dark);
  font-size: 1.6rem;
  line-height: 2.4rem;
  font-family: var(--main-ff);
  background-color: var(--color-white);
}

img {
  max-width: 100%;
  height: auto;
  vertical-align: middle;
}

h1 {
  margin: 0;
  font-weight: 700;
  line-height: 1.2;
  font-family: var(--main-ff);
}

h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  font-weight: 600;
  font-family: var(--main-ff);
}

a {
  text-decoration: none;
  color: inherit;
  outline: none;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.list-rest {
  padding: 0;
  margin: 0;
  list-style: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  clip: rect(0 0 0 0);
}

.site-container {
  width: 100%;
  max-width: 1526px;
  margin: 0 auto;
  padding: 0 20px;
}

@media screen and (max-width: 1367px) {
  .site-container {
    max-width: 1170px;
  }
}

@media screen and (max-width: 767px) {
  .site-container .site-content {
    padding: 0;
  }
}

.site-content {
  width: 100%;
  max-width: 1270px;
  margin: 0 auto;
  padding: 0 20px;
}

@media screen and (max-width: 1367px) {
  .site-content {
    max-width: 1170px;
  }
}

.grid {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  gap: var(--grid-gap);
}

.page-wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  overflow: hidden;
}

.page-wrapper ::selection {
  background-color: var(--color-purple);
  color: var(--color-white);
}

.page-main {
  flex-grow: 1;
}

.header__top-block {
  display: flex;
  flex-wrap: wrap;
  flex-wrap: wrap;
  align-items: center;
  padding-top: 5rem;
  padding-bottom: 4rem;
  border-bottom: 6px solid var(--color-green-light);
}

.header__top-block-info-wrap {
  display: flex;
  align-items: center;
  width: 100%;
  margin-left: 27.5rem;
  margin-bottom: 2px;
}

@media screen and (max-width: 992px) {
  .header__top-block-info-wrap {
    margin-left: 0;
    margin-bottom: 2.3rem;
  }
}

@media screen and (max-width: 992px) {
  .header__top-block {
    border-color: var(--color-white);
    border-width: 4px;
  }
}

.header__logo {
  transition: transform var(--main-transition);
}

.header__logo:focus {
  transform: scale(1.1);
}

@media screen and (max-width: 992px) {
  .header__logo-decktop {
    display: none;
  }
}

.header__logo-mobile {
  display: none;
}

@media screen and (max-width: 992px) {
  .header__logo-mobile {
    display: block;
  }
}

.header__burger-button {
  display: flex;
  align-items: center;
  margin-right: 2rem;
  font-weight: 600;
  font-size: 1.4rem;
  line-height: 1.9rem;
  text-transform: uppercase;
  cursor: pointer;
  outline: none;
}

.header__burger-button:hover .header__burger-icon {
  fill: var(--color-purple);
}

.header__burger-button:hover .header__dropdown-content {
  opacity: 1;
  visibility: visible;
}

.header__burger-button:focus .header__burger-icon {
  fill: var(--color-purple);
}

.header__burger-button:focus .header__dropdown-content {
  opacity: 1;
  visibility: visible;
}

@media screen and (max-width: 992px) {
  .header__burger-button {
    display: none;
  }
}

.header__burger-icon {
  display: block;
  margin-right: 1.5rem;
  fill: var(--color-green-light);
  transition: fill var(--main-transition);
}

.header__city {
  display: flex;
  align-items: center;
  font-weight: 600;
  font-size: 1.4rem;
  line-height: 1.9rem;
  text-transform: uppercase;
  outline: none;
}

.header__city:hover .city-choose__icon {
  fill: var(--color-purple);
}

.header__city:hover .header__dropdown-content {
  opacity: 1;
  visibility: visible;
}

.header__city:focus .city-choose__icon {
  fill: var(--color-purple);
}

.header__city:focus .header__dropdown-content {
  opacity: 1;
  visibility: visible;
}

@media screen and (max-width: 992px) {
  .header__city {
    display: none;
  }
}

.header__tel {
  display: inline-block;
  margin-right: 3.5rem;
  margin-left: auto;
  font-weight: 600;
  font-size: 3rem;
  line-height: 120%;
  transition: color var(--main-transition);
}

.header__tel:hover {
  color: var(--color-green-light);
}

.header__tel:focus {
  color: var(--color-green-light);
}

@media screen and (max-width: 992px) {
  .header__tel {
    display: none;
  }
}

.header__offices-button {
  margin-right: 1.7rem;
  background-color: transparent;
}

.header__offices-button .icon-marker {
  width: 2.5rem;
  height: 2.5rem;
}

@media screen and (max-width: 992px) {
  .header__offices-button {
    justify-content: center;
    width: 5.33333rem;
    height: 5.33333rem;
    padding: 0;
    order: 3;
  }
  .header__offices-button span {
    display: none;
  }
  .header__offices-button .icon-marker {
    margin-right: 0;
    width: 3.16667rem;
    height: 3.16667rem;
  }
}

.header__web-bank {
  background-color: transparent;
}

.header__web-bank:hover .header__dropdown-content, .header__web-bank:focus .header__dropdown-content {
  left: auto;
  right: 0;
  opacity: 1;
  visibility: visible;
}

.header__web-bank:hover .header__dropdown-content-item, .header__web-bank:focus .header__dropdown-content-item {
  color: var(--color-dark);
}

.header__web-bank .header__dropdown-content {
  left: auto;
  right: 0;
}

@media screen and (max-width: 992px) {
  .header__web-bank {
    padding: 1.66667rem;
    order: 1;
    margin-left: auto;
    margin-right: 1.66667rem;
  }
}

.header__bottom-block {
  position: relative;
  display: flex;
  align-items: center;
  border-bottom: 1px solid var(--border-color);
  overflow: hidden;
}

.header__bottom-block-links {
  padding: 3.4rem 0;
  transition: transform var(--main-transition), opacity var(--main-transition), visibility var(--main-transition);
}

.header__bottom-block-links._is-hidden {
  transform: translateY(11.3rem);
  opacity: 0;
  visibility: hidden;
}

@media screen and (max-width: 767px) {
  .header__bottom-block-links {
    padding: 3.33333rem 0 2.33333rem 0;
  }
}

.header__bottom-block-form {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  padding: 3.4rem 0;
  opacity: 0;
  visibility: hidden;
  transition: opacity var(--main-transition), visibility var(--main-transition);
}

.header__bottom-block-form-input {
  color: var(--color-dark);
  font-weight: 600;
  font-size: 3.2rem;
  line-height: 4.4rem;
  background-color: transparent;
  border: none;
  border-radius: 0;
  outline: none;
}

.header__bottom-block-form-input::placeholder {
  color: var(--color-grey);
}

.header__bottom-block._is-active .header__bottom-block-form {
  opacity: 1;
  visibility: visible;
}

.header__bottom-block._is-active .header__search-button-hide {
  opacity: 1;
  visibility: visible;
}

@media screen and (max-width: 992px) {
  .header__bottom-block {
    border-color: rgba(255, 255, 255, 0.5);
  }
}

.header__search-button-hide {
  position: relative;
  z-index: 3;
  margin-left: auto;
  width: 2.8rem;
  height: 2.8rem;
  opacity: 0;
  visibility: hidden;
  transition: opacity var(--main-transition), visibility var(--main-transition);
  cursor: pointer;
}

.header__search-button-hide span {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 100%;
  height: 3px;
  border-radius: 4px;
  background-color: var(--color-green-light);
  transform: translate(-50%, -50%) rotate(45deg);
  display: block;
  transition: background-color var(--main-transition);
}

.header__search-button-hide span:last-child {
  transform: translate(-50%, -50%) rotate(-45deg);
}

@media screen and (max-width: 992px) {
  .header__search-button-hide span {
    background-color: var(--color-white);
  }
}

.header__search-button-hide:hover span {
  background-color: var(--color-purple);
}

.header__search-button-hide:focus span {
  background-color: var(--color-purple);
}

.header__search-button {
  position: relative;
  z-index: 3;
  margin-left: 3rem;
  cursor: pointer;
}

.header__search-button:hover .header__search-icon {
  fill: var(--color-purple);
}

.header__search-button:focus .header__search-icon {
  fill: var(--color-purple);
}

@media screen and (max-width: 992px) {
  .header__search-button:focus .header__search-icon {
    fill: var(--color-green-light);
  }
}

.header__search-icon {
  display: block;
  width: 2.8rem;
  height: 2.8rem;
  fill: var(--color-green-light);
  transition: fill var(--main-transition);
}

@media screen and (max-width: 992px) {
  .header__search-icon {
    fill: var(--color-white);
  }
}

.header__bottom-block-link {
  margin-right: 3.2rem;
  font-weight: 600;
  font-size: 3.2rem;
  line-height: 4.4rem;
  transition: color var(--main-transition);
}

.header__bottom-block-link._is-active {
  color: var(--color-green-light);
}

.header__bottom-block-link:hover {
  color: var(--color-green-light);
}

.header__bottom-block-link:focus {
  color: var(--color-green-light);
}

.header__bottom-block-link:nth-last-of-type(1) {
  margin-right: 0;
}

@media screen and (max-width: 992px) {
  .header__bottom-block-link {
    color: rgba(255, 255, 255, 0.5);
  }
  .header__bottom-block-link._is-active {
    color: var(--color-white);
  }
}

@media screen and (max-width: 767px) {
  .header__bottom-block-link {
    font-size: 3rem;
    line-height: 3.5rem;
  }
}

.header__dropdown {
  position: relative;
}

.header__dropdown-content {
  position: absolute;
  top: 100%;
  left: 0;
  z-index: 15;
  padding-top: 1rem;
  width: 27rem;
  transition: opacity var(--main-transition), visibility var(--main-transition);
  opacity: 0;
  visibility: hidden;
}

.header__dropdown-content-inner {
  width: 100%;
  padding: 1.5rem 0;
  background-color: var(--color-white);
  border-radius: var(--border-r);
}

.header__dropdown-content-item {
  display: block;
  font-weight: 400;
  font-size: 1.8rem;
  line-height: 120%;
  padding: .9rem 2.6rem;
  text-transform: none;
  transition: background-color var(--main-transition);
}

.header__dropdown-content-item:hover {
  background-color: #f5f5f5;
}

.header__burger-button-mobile {
  position: relative;
  display: none;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 4rem;
  height: 4rem;
}

.header__burger-button-mobile span {
  display: block;
  width: 3rem;
  height: 2px;
  background-color: var(--color-white);
}

.header__burger-button-mobile span:nth-child(2) {
  margin: 5px 0;
}

@media screen and (max-width: 992px) {
  .header__burger-button-mobile {
    display: flex;
  }
}

.header__tel-mobile {
  display: none;
  order: 2;
  align-items: center;
  justify-content: center;
  width: 5.33333rem;
  height: 5.33333rem;
  border-radius: var(--border-r);
  border: 1px solid var(--color-white);
}

.header__tel-mobile .icon-phone {
  fill: var(--color-white);
  width: 2.66667rem;
  height: 2.66667rem;
}

@media screen and (max-width: 992px) {
  .header__tel-mobile {
    display: flex;
    margin-right: 1.66667rem;
  }
}

.header__button-contacts {
  display: none;
  align-items: center;
  justify-content: center;
  width: 5.33333rem;
  height: 5.33333rem;
  border-radius: var(--border-r);
  border: 1px solid var(--color-white);
  order: 4;
}

.header__button-contacts .icon-chat {
  fill: none;
  stroke: var(--color-white);
  stroke-width: 1px;
  width: 2.33333rem;
  height: 2.33333rem;
}

@media screen and (max-width: 992px) {
  .header__button-contacts {
    display: flex;
  }
}

@media screen and (max-width: 992px) {
  .header {
    color: var(--color-white);
  }
}

@media screen and (max-width: 992px) {
  .header_color-green {
    color: var(--color-dark);
  }
  .header_color-green .header__burger-button-mobile span {
    background-color: var(--color-green-light);
  }
  .header_color-green .button {
    border-color: var(--color-green-light);
    color: var(--color-green-light);
  }
  .header_color-green .header__tel-mobile,
  .header_color-green .header__button-contacts {
    border-color: var(--color-green-light);
  }
  .header_color-green .header__web-bank:hover {
    background-color: transparent;
  }
  .header_color-green .header__logo-decktop {
    display: block;
  }
  .header_color-green .header__logo-mobile {
    display: none;
  }
  .header_color-green .header__button-contacts .icon {
    stroke: var(--color-green-light);
  }
  .header_color-green .header__offices-button .icon {
    fill: var(--color-green-light);
  }
  .header_color-green .header__tel-mobile .icon {
    fill: var(--color-green-light);
  }
  .header_color-green .header__top-block {
    border-color: var(--color-green-light);
  }
  .header_color-green .header__bottom-block-link {
    color: rgba(0, 0, 0, 0.4);
  }
  .header_color-green .header__bottom-block-link._is-active {
    color: var(--color-dark);
  }
  .header_color-green .header__search-icon {
    fill: rgba(0, 0, 0, 0.4);
  }
  .header_color-green .header__search-button-hide span {
    background-color: rgba(0, 0, 0, 0.4);
  }
}

.header_absolute {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 99;
  width: 100%;
}

.city-choose {
  cursor: pointer;
}

.city-choose__icon {
  display: block;
  margin-right: 0.8rem;
  fill: var(--color-green-light);
  transition: fill var(--main-transition);
}

.footer__top-block {
  padding: 4rem 0 3.1rem 0;
  border-top: 6px solid var(--color-green-light);
  border-bottom: 1px solid var(--border-color);
}

@media screen and (max-width: 992px) {
  .footer__top-block {
    display: none;
  }
}

.footer__middle-block {
  row-gap: 0;
  padding: 4.4rem 0 4rem 0;
  border-bottom: 1px solid var(--border-color);
}

@media screen and (max-width: 992px) {
  .footer__middle-block {
    padding-top: 0;
    row-gap: 4rem;
